.columns
{
  column-gap: var(--space-between-content-multicolumns);
  &.auto{
    column-width: 37ch;
  }
  h2{
    column-span: all;
    margin-top: var(--space-xl);
    max-width: var(--measure);
    &:first-child{
      margin-top: calc( var(--space-xl) - var(--space-between-blocks) );
    }
  }
}
@media screen and (min-width: 640px){
  .columns
  {
    column-gap: var(--space-between-content-multicolumns);
    &.xxs{
      column-width: 5ch;
    }
    &.xs{
      column-width: 10ch;
    }
    &.s{
      column-width: 15ch;
    }
    &.m{
      column-width: 20ch;
    }
    &.l{
      column-width: 25ch;
    }
    &.xl{
      column-width: 30ch;
    }
    &.xxl{
      column-width: 35ch;
    }
  }
}
